@import "./variables";

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%; //Default font size  1rem = 10px  10/16px
  scroll-behavior: smooth;

  @media only screen and (max-width: $bp-largest) {
    font-size: 50%;
  }
}

body {
  font-family: $font-primary;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh;
  overflow-x: hidden;
}

.theme--evening {
  background: linear-gradient(
    195deg,
    $color-bg-evening-1 20.25%,
    $color-bg-evening-2 40.22%,
    $color-bg-evening-2 50.22%,
    $color-bg-evening-3 120.42%,
    $color-bg-evening-4 132.08%
  );
}

.theme--night {
  background: linear-gradient(
    195deg,
    $color-bg-night-1 20%,
    $color-bg-night-2 45%,
    $color-bg-night-3 105%,
    $color-bg-night-4 132%
  );
}
