@import "../../sass/variables";
@import "../../sass/animations";
.moon {
  grid-row: 2 / span 1;
  grid-column: 2 / span 1;
  align-self: center;
  justify-self: end;
  width: 15rem;
  height: 15rem;
  background-color: $color-moon;
  border-radius: 50%;
  animation: smallPlanetMoves 5s ease-out infinite;
  transition: all 0.2s;
  //animation: planetMoviesVertical 5s ease-out infinite;
  @media only screen and (max-width: $bp-small) {
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
  }

  &::before,
  &::after {
    content: "";
    border-radius: 50%;
    animation: planetMoviesVertical 5s ease-out infinite;
    background-color: rgba($color-moon, 0.2);
    position: absolute;
  }
  &::before {
    width: 40rem;
    height: 40rem;

    top: -12.5rem;
    left: -12.5rem;
    z-index: -1;
    filter: blur(40px);
  }
  &::after {
    width: 30rem;
    height: 30rem;
    top: -7.5rem;
    left: -7.5rem;
    z-index: -1;
    filter: blur(20px);
  }

  &__crater {
    width: 6rem;
    height: 2rem;
    background-color: $color-moon-crater;
    position: absolute;
    border-radius: 2rem;
    z-index: 2;

    transform: translate(-50%, -50%);

    &--top {
      top: 25%;
      left: 60%;
    }
    &--mid {
      top: 50%;
      left: 35%;
    }
    &--bottom {
      top: 75%;
      left: 60%;
    }
  }
}
