@import "../sass/variables";
.heading-1 {
  font-size: 5.5rem;
  font-weight: 400;
  padding: 1rem 2rem;
  line-height: 1;
  justify-self: start;

  &--evening {
    background-color: $color-heading-bg-evening;
  }
  &--night {
    background-color: $color-heading-bg-night;
    color: $color-heading-bg-evening;
  }
}

.heading-2 {
  font-size: 3rem;
  font-weight: 500;
  line-height: 1;
}
.heading-3 {
  font-size: 2.5rem;
  font-weight: 500;
  line-height: 1;
  padding: 2rem 0;
}

.text {
  &--strong {
    font-weight: 600;
  }
  &--evening {
    color: $color-heading-txt-evening;
  }
  &--night {
    color: $color-heading-txt-night;
  }
}

.content-wrapper__subtext {
  font-size: 1.6rem;
  font-weight: 500;
}
