@import "../../sass/variables";
@import "../../sass/animations";
.earth {
  grid-row: 2 / span 1;
  grid-column: 2 / span 1;
  align-self: center;
  justify-self: end;
  width: 15rem;
  height: 15rem;
  background-color: $color-earth;
  border-radius: 50%;
  animation: smallPlanetMoves 5s ease-out infinite;
  box-shadow: 0 2rem 4rem rgba(#000, 0.2);
  position: relative;
  z-index: 10;

  @media only screen and (max-width: $bp-small) {
    width: 10rem;
    height: 10rem;
    position: absolute;
    left: 28rem;
    top: 10rem;
    z-index: -1;
  }

  &::before,
  &::after {
    content: "";
    border-radius: 50%;
    animation: planetMoviesVertical 5s ease-out infinite;
    background-color: rgba($color-earth-continent-2, 0.1);
    position: absolute;
  }
  &::before {
    width: 22rem;
    height: 22rem;
    top: -3.5rem;
    left: -3.5rem;
    z-index: -1;
    @media only screen and (max-width: $bp-small) {
      width: 16rem;
      height: 16rem;
      top: -3rem;
      left: -3rem;
    }
  }
  &::after {
    width: 30rem;
    height: 30rem;
    top: -7.5rem;
    left: -7.5rem;
    z-index: -1;
    @media only screen and (max-width: $bp-small) {
      width: 22rem;
      height: 22rem;
      top: -6rem;
      left: -6rem;
    }
  }

  &__continent {
    width: 6rem;
    height: 2rem;
    background-color: $color-earth-continent-1;
    position: absolute;
    border-radius: 2rem;
    z-index: 2;

    transform: translate(-50%, -50%);

    &--top {
      top: 25%;
      left: 60%;
    }
    &--mid {
      top: 50%;
      left: 35%;
      background-color: $color-earth-continent-2;
    }
    &--bottom {
      top: 75%;
      left: 60%;
    }
    @media only screen and (max-width: $bp-small) {
      width: 4rem;
      height: 1rem;
    }
  }
}
