@import "../../sass/variables";

.skills {
  &__container {
    grid-column: 1/-1;

    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(40rem, 60rem));
    column-gap: 3rem;

    row-gap: 5rem;

    justify-content: space-between;
    padding: 10rem 0;
    @media only screen and (max-width: $bp-lp) {
      justify-content: center;
    }
    @media only screen and (max-width: $bp-small) {
      grid-template-columns: 1fr;
      grid-template-rows: repeat(4, min-content);
      justify-content: start;
      column-gap: 0;
    }
  }
  &__category {
    justify-self: center;
    @media only screen and (max-width: $bp-lp) {
      text-align: center;
    }
    @media only screen and (max-width: $bp-small) {
      text-align: left;
      justify-self: start;
    }
  }

  &__header {
    font-weight: 500;
    text-transform: uppercase;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    @media only screen and (max-width: $bp-small) {
      text-align: left;
    }
  }
}
