@import "./sass/variables";
.container {
  position: relative;
  z-index: 5;
  display: grid;
  grid-template-rows: 5rem calc(97vh - 10rem) min-content min-content;
  grid-template-columns:
    1fr [center-start] repeat(
      9,
      [col-start] minmax(min-content, 14rem) [col-end]
    )
    [center-end] 1fr;
  color: #fff;
  font-size: 2rem;
  gap: 0.7rem;

  @media only screen and (max-width: $bp-small) {
    grid-template-columns:
      [center-start] repeat(9, [col-start] minmax(min-content, 14rem) [col-end])
      [center-end];
    grid-template-rows: 6rem calc(95vh - 15rem) min-content min-content;
  }
}
