@import "../../sass/variables";

.settings {
  width: 100%;
  height: 100%;
  grid-column: 1 / -1;
  grid-row: 1 / span 1;
  display: flex;
  justify-content: center;
  align-items: center;

  &__btn {
    min-width: 15rem;
    border: none;
    padding: 1.2rem 1.5rem;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 1px;
    font-weight: 500;
    margin: 0 0.5rem;

    cursor: pointer;
    transition: all 0.4s;

    &:active {
      transform: translateY(0.4rem);
    }
    &--evening {
      background-color: $color-heading-txt-evening;
    }
    &--night {
      background-color: $color-heading-bg-night;
    }
  }
}
