@import "../../sass/variables";
.list {
  &__item {
    &:not(:first-child) {
    }
    margin: 0.5rem;
    padding: 1rem 2rem;

    font-size: 1.6rem;
    font-weight: 500;
    color: black;
    list-style: none;

    display: flex;
    align-items: center;
    justify-content: space-between;

    cursor: default;

    &--evening {
      background-color: rgba($color-heading-txt-evening, 1);
    }
    &--night {
      background-color: rgba($color-heading-txt-night, 1);
    }
  }

  &__icon {
    display: inline-block;
    height: 2.5rem;
    width: 2.5rem;

    fill: $color-bg-night-3;
  }
  &__text {
    margin-left: 0.5rem;
    &::selection {
      color: $color-heading-txt-evening;
      background: $color-bg-evening-1;
    }
  }
  &__link {
    transition: all 0.2s;

    &:visited,
    &:link {
      text-decoration: none;
    }
    &:active {
      transform: translateY(2px);
    }
    &:hover {
      background-color: white;
    }
  }
}
