//INDIVIDUAL PROJECT
.project {
  grid-column: 1/-1;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(50rem, 1fr));
  gap: 2rem;

  &__images {
    display: grid;
    grid-template: repeat(4, 1.5fr) / repeat(6, 1fr);
    gap: 1rem;
    background-color: rgba(#000, 0.01);
    &-container {
      &--1 {
        grid-column: 1 / span 3;
        grid-row: 1 / span 2;
      }
      &--2 {
        grid-column: 4 / span 3;
        grid-row: 1 / span 2;
      }
      &--3 {
        grid-column: 1 / span 3;
        grid-row: 3 / span 2;
      }
      &--4 {
        grid-column: 4 / span 3;
        grid-row: 3 / span 2;
      }
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: fill;
  }

  &__content {
    display: grid;
    grid-template-rows: repeat(4, min-content);
  }
  &__header {
    text-transform: uppercase;
  }
  &__description {
    font-size: 1.7rem;
    line-height: 1.3;
    font-weight: 500;

    padding: 2rem 0;
    &::selection {
      color: $color-heading-txt-evening;
      background: $color-bg-evening-1;
    }
  }
  &__tag-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(10rem, 15rem));
    gap: 0.5rem;

    margin-top: 1rem;

    font-size: 1.5rem;
  }
  &__tag {
    padding: 0.7rem 0.5rem;
    background-color: rgba(#000, 0.2);
    text-align: center;
    cursor: default;
    &::selection {
      color: $color-heading-txt-evening;
      background: $color-bg-evening-1;
    }
  }
  &__link-container {
    display: flex;
    justify-content: flex-end;
    a {
      margin-right: 0.5rem;
    }
  }
}
