@import "../../sass/variables";

.contact {
  &__container {
    grid-column: 1/-1;

    display: grid;
    grid-template-columns: minmax(40rem, 80rem);

    padding: 10rem 0;
    justify-content: center;
  }
  &__form {
    .form {
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: repeat(2, min-content) 1fr;
      row-gap: 2rem;
      justify-items: stretch;
      background-color: rgba(#000, 0.3);
      padding: 4rem 3rem;

      &__group {
        display: flex;
        flex-direction: column;
        position: relative;
      }
      &__btn {
        font-family: inherit;
        font-weight: 500;
        font-size: 1.8rem;
        color: #fff;

        border: none;

        padding: 1.3rem 5rem;
        box-shadow: 0 2rem 4rem rgba(#000, 0.01);
        background-color: rgba(#000, 0.3);

        justify-self: center;

        transition: all 0.3s;
        cursor: pointer;
        &:hover {
          background-color: white;
          color: black;
        }
        &:active {
          border-bottom: 3px solid transparent;
          background-color: rgba(#000, 1);
          transform: translateY(2px);
        }
      }

      .input {
        &__text {
          margin-bottom: 1rem;
          font-size: 1.6rem;
          font-weight: 600;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 5px;
        }
        &__box,
        &__textarea {
          display: block;

          padding: 2rem 2rem;

          font-size: 1.6rem;
          font-family: inherit;
          font-weight: 400;
          color: #fff;

          border: none;
          box-shadow: 0 2rem 4rem rgba(#000, 0.01);
          background-color: rgba(#000, 0.1);
          border-bottom: 3px solid transparent;
          transition: all 0.3s;

          &--evening {
            &:focus {
              outline: none;
              border-bottom: 3px solid $color-heading-txt-evening;
            }
            &:focus:invalid {
              border-bottom: 3px solid red;
            }
          }
          &--night {
            &:focus {
              outline: none;
              border-bottom: 3px solid $color-heading-txt-night;
            }
            &:focus:invalid {
              border-bottom: 3px solid red;
            }
          }
        }

        &__textarea {
          height: 25rem;
          resize: none;
        }
      }
    }
  }
  &__links {
    display: grid;
    grid-template-columns: repeat(3, max-content);
    justify-content: center;
    align-items: center;
  }
}
